import clsx from 'clsx';
import { useState } from 'react';
import { Switch } from '~/@headlessui/react/dist';
import { ToggleProps } from './toggle.interface';

export const Toggle: React.FC<ToggleProps> = ({ toggleVariant = 'simple', label, enabled, onChange }) => {
  const [isenabled, setEnabled] = useState(enabled);

  const handleChange = (value: boolean) => {
    setEnabled(value);
    if (onChange) {
      onChange(value);
    }
  };

  const baseClasses = clsx(
    `${
      isenabled ? ' zep-bg-indigo-700' : 'zep-bg-greyscale-300'
    } zep-relative zep-inline-flex zep-h-1.5 zep-w-[44px] zep-shrink-0 zep-cursor-pointer zep-border-2 zep-border-transparent zep-duration-200 zep-ease-in-out focus:zep-outline-none focus:zep-ring-2 focus:zep-ring-indigo-500 focus:zep-ring-offset-2 zep-items-center zep-rounded-full zep-transition-colors`,
  );

  const knobClasses = clsx(
    `${
      isenabled ? 'zep-translate-x-1.25' : 'zep-translate-x-0'
    } zep-relative zep-pointer-events-none zep-inline-block zep-h-[20px] zep-w-[20px] zep-transform zep-rounded-full zep-ring-0 zep-duration-200 zep-ease-in-out zep-shadow-sm zep-bg-greyscale-0 zep-transition-colors`,
  );

  const renderIcons = () => {
    return (
      <span className={knobClasses}>
        {/* Inactive state: Show cross icon */}
        {!isenabled && (
          <span aria-hidden="true" className="zep-flex zep-w-full zep-h-full zep-items-center zep-justify-center">
            <svg fill="none" viewBox="0 0 12 12" className="zep-h-0.75 zep-w-0.75 zep-text-gray-400">
              <path
                d="M4 8l2-2m0 0l2-2M6 6L4 4m2 2l2 2"
                stroke="currentColor"
                strokeWidth={2}
                strokeLinecap="round"
                strokeLinejoin="round"
              />
            </svg>
          </span>
        )}

        {/* Active state: Show tick icon */}
        {isenabled && (
          <span aria-hidden="true" className="zep-flex zep-w-full zep-h-full zep-items-center zep-justify-center">
            <svg fill="currentColor" viewBox="0 0 12 12" className="zep-h-0.75 zep-w-0.75 zep-text-indigo-600">
              <path d="M3.707 5.293a1 1 0 00-1.414 1.414l1.414-1.414zM5 8l-.707.707a1 1 0 001.414 0L5 8zm4.707-3.293a1 1 0 00-1.414-1.414l1.414 1.414zm-7.414 2l2 2 1.414-1.414-2-2-1.414 1.414zm3.414 2l4-4-1.414-1.414-4 4 1.414 1.414z" />
            </svg>
          </span>
        )}
      </span>
    );
  };

  // Handle left or right label
  if (toggleVariant === 'leftLabel' || toggleVariant === 'rightLabel') {
    return (
      <div className="zep-flex zep-items-center zep-gap-0.5">
        {toggleVariant === 'leftLabel' && (
          <div className="zep-order-1">
            {label && <p className="zep-text-sm zep-font-medium">{label}</p>}
          </div>
        )}

        <Switch checked={isenabled} onChange={handleChange} className={`${baseClasses} zep-order-2`}>
          <span className="zep-sr-only">{label || 'Toggle'}</span>
          <span aria-hidden="true" className={knobClasses} />
        </Switch>

        {toggleVariant === 'rightLabel' && (
          <div className="zep-order-2">
            {label && <p className="zep-text-sm zep-font-medium">{label}</p>}
          </div>
        )}
      </div>
    );
  }

  // Handle withIcon variant
  if (toggleVariant === 'withIcon') {
    return (
      <Switch
        checked={isenabled}
        onChange={handleChange}
        className={`${baseClasses} zep-border-2 zep-border-transparent`}
      >
        <span className="zep-sr-only">{label || 'Toggle'}</span>
        {renderIcons()}
      </Switch>
    );
  }

  // Default toggle simple
  return (
    <Switch
      checked={isenabled}
      onChange={handleChange}
      className={`${baseClasses} zep-border-2 zep-border-transparent`}
    >
      <span className="zep-sr-only">{label || 'Toggle'}</span>
      <span aria-hidden="true" className={knobClasses} />
    </Switch>
  );
};
